*{
    padding:0;
    margin:0;
}

.container{
    display:flex;
    gap:10px;
    margin:10px 15% 0px 15%;
    /* flex-wrap: wrap; */
}

.item{
    display: flex;
    border-radius: 5px;
    padding:5px;
    flex:5;
}

.buttons{
    display: flex;
    gap:4px;
    flex:1;
}

button{
    padding:5px;
    border-radius: 5px;
    border:none;
}